<template>
  <div id="app">
    <!-- <NavBar /> -->
    <MainPage />
  </div>
</template>

<script>
import MainPage from "./components/MainPage.vue";
import { mapActions } from "vuex";
import { KAVACH_SERVER_BASE_URL } from './config'

export default {
  name: "App",
  components: {
    MainPage,
  },
  methods: {
    ...mapActions(["setSession"]),
  },
  async created() {
    // this.setSession();
      const resp = await fetch(KAVACH_SERVER_BASE_URL+"/api/v1/aadhaar/session", {
        method:'Post',
        headers:{
          'Credentials': 'include',
          'Content-Type': 'application/json',
          'withCredentials': 'true'
        }
      });
    //   Cookie from response
      const cookie = resp.headers.get("set-cookie");
      console.log(cookie);
  },
};
</script>

